.user-privew {
    display: flex;
    gap: 2px;
    margin-top: 8px;
    background-color: rgb(217, 229, 240);
    border: solid 2px black;
    height: 29px;
    width: 734px;
}

.user-privew * {
    width: 190px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 16px;
    color: rgb(0, 0, 113);
    font-weight: bold;
}

.user-privew .user-name {
    width: 100px;
}

.user-privew .icon {
    width: 30px;
    height: 25px;
    margin-left: 10px;
}

.user-privew .display {
    height: 25px;
    width: 70px;
    background-color: rgb(216, 221, 231);
    border: solid 1px;
    border-color: black white white black;
}

.user-privew .btn {
    font-size: 14px;
    height: 27px;
    width: 130px;
    background-color: rgb(193, 221, 239);
    /* color: rgb(0, 0, 40); */
    border: solid 1px;
    border-color: white black black white;
    cursor: pointer;
}

.user-privew .btn:hover {
    background-color: rgb(149, 213, 243);
}

.user-privew .btn:active {
    border-color: black rgb(113, 113, 113) rgb(113, 113, 113) black;
}

